body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
    text-decoration: none;
    color: black;

}

#navBar {
    background-color: #f8f8f8;
    border-bottom: 1px solid #e7e7e7;
    margin-bottom: 20px;
}

#cart-cluster {
    position: absolute;
    right: 20px;
    top: 30px;
}

.fluid {
    max-width: 40vw;
    margin: 15px 10px;
}

#cartimg {
    max-width: 50px;
    margin-left: 10px;
}


@media (min-width: 900px) { 
    .fluid {
        width: 25vw;
    }
    #cart-cluster {
        position: absolute;
        right: 50px;
    }
}

@media (min-width: 1500px) { 
    .fluid {
        margin-left: 100px;
    }
    #cart-cluster {
        position: absolute;
        right: 100px;
        top: 50px;
    }
}

@media (min-width: 2000px) { 
    .fluid {
        margin-left: 400px;
    }
    #cart-cluster {
        position: absolute;
        right: 300px;
        top: 70px;
    }
}

@media (max-width: 440px) {
    #cart-cluster {
        position: absolute;
        right: 20px;
        top: 15px;
    }
    .fluid {
        width: 45vw;
    }
}


#footer-body {
    border-top: solid 1px #e7e7e7;
    color: #848484;
    min-height: 20px;
    width: 100%;
    text-align: center;
    margin-top: 30px;
    padding-top: 30px;
}

.footer-col {
   margin-bottom: 50px;
}

.footer-link {
    color: #848484;
}

.social-icon {
    max-height: 30px;
    max-width: 30px;
    margin: 0 5px;
    -webkit-transition-duration: .3s;
            transition-duration: .3s;
}

.social-icon:hover {
    -webkit-transform: scale(1.3) rotate(360deg);
            transform: scale(1.3) rotate(360deg);
}


@media (min-width: 768px) {
    .footer-col {
        margin-bottom: 10px;
    }
}

.card-text {
    text-align: center;
}

.max-quantity-indicator {
    color: red;
    font-size: 12px;
}

a:hover {
    text-decoration: none;
}

a {
    text-decoration: none !important;
}

.filter-group {
    border-bottom: 1px solid #e7e7e7;
    word-wrap: break-word;
}

#load-more {
    text-align: center;
}

#load-more-button {
   margin: 10px auto 20px auto;
   max-width: 400px;
   width: 100%;
}

.pager-comp {
    display: inline;
}

.cart-quantity-button {
    padding: .4em 1.2em .6em;
}

#increment-button-group {
    display: inline;
}

#filter-title {
    text-align: center;
}

@media (min-width: 1400px) and (max-width:1600px){
    #filter {
        max-width: 12vw;
    }
}

@media (min-width: 1600px) and (max-width:2000px){
    #filter {
        max-width: 10vw;
    }
}

@media (min-width: 2000px) and (max-width:5000px){
    #filter {
        max-width: 8vw;
    }
}

@media (min-width: 1200px) and (max-width: 1216px){
    #increment-button-group {
        display: block;
        margin: 5px 0px 10px 5px;
    }
}

@media (max-width: 767px){
    #filter-body{
        max-height: 75vh;
        overflow-y: scroll;
    }
}

#or {
    position: relative;
    width: 300px;
    height: 50px;
    margin: 0px auto;
    line-height: 50px;
    text-align: center;
}

#or::before,
#or::after {
    position: absolute;
    width: 130px;
    height: 1px;

    top: 24px;

    background-color: #aaa;

    content: '';
}

#or::before {
    left: 0;
}

#or::after {
    right: 0;
}
.cart-image {
    width: 60%;
}

.cart-image-col {
    text-align: center;
}

.cartitem {
    margin: 15px;
    padding: 10px;
    border-bottom: solid 1px #e7e7e7;
}

.cart-quantity-button:hover {
    cursor: pointer;
}


.cart-item-description {
    margin-top: 10px;
}

.add:active {
    background-color: #529152;
}

.remove:active {
    background-color: #b24d4a;
}

.remove-all-button {
    margin-left: 5px;
    margin-top: 8px;
}

.add {
    margin-right: 3px;
}

.remove {
    margin-left: 3px;
}

#empty-cart-jumbotron {
    width: 70%;
    margin: 10px auto 0px auto;
    text-align: center;
}

#checkout-button {
    width: 100%;
}

#total-summary {
    text-align: center;
}

#cart-title {
    text-align: center;
}

@media(min-width: 768px) {
    .cart-image {
        margin-left: auto;
    }
    .cart-item-text {
        margin-right: auto;
    }
}

@media (min-width: 600px){
    #cart{
        width: 70%;
    }
}
#related-products-label {
    text-align: center;
    border-bottom: 1px solid #e7e7e7;
}

.max-quantity-indicator {
    color: red;
    font-size: 12px;
}
#form {
    margin: 10px auto;
    text-align: center;
    width: 300px;
}

.form-input-group {
    display: block;
}

.form-input {
    display: block;
    margin: 10px auto;
    padding-left: 5px;
    width: 95%;
    font-weight: normal;
}

.invalid {
    border: solid 2px red;
}

#submit-form {
    width: 100%;
}

#state-input {
    height: 30px;
    background-color: white;
}
/* Cart Items Column */
#cart-col {
    max-height: 75vh;
    overflow-y: auto;
}

#cart-col-head {
    text-align: center;
}

.cart-col-item {
    border-bottom: 1px solid #e7e7e7;
    padding: 15px;
}

.cart-item-img {
    max-width: 100%;
}

.cart-quantity-button:hover {
    cursor: pointer;
}

.add:active {
    background-color: #529152;
}

.remove:active {
    background-color: #b24d4a;
}

.remove-all-button {
    margin-left: 5px;
    margin-top: 8px;
}

.add {
    margin-right: 3px;
}

.remove {
    margin-left: 3px;
}


@media (max-width: 767px) {
    #cart-col {
        height: 45vh;
    }
}

@media (max-width: 450px) {
    #cart-col {
        height: 35vh;
    }
}

/* Customer Info Column */

#info-col {
    text-align: center;
}

#info {
    font-size: 18px;
}

#edit-info-button {
    width: 100%;
}

@media (min-width: 330px) and (max-width: 767px) {
    #edit-info-button {
        width: 250px;
    }
}

/* Payment Column */

#pay-col {
    text-align: center;
}

#price {
    font-size: 45px;
}

#shipping-cost {
    margin-top: -10px;
}
#awaiting-confirmation {
   margin: 15vh 0px;
}

#order-confirmation-jumbotron {
    max-width: 85vw;
    margin: 5vh auto;
    text-align: center;
}

#go-home-button {
    margin-top: 30px;
}
